<template>
  <div ref='wrapper'>
     <slot></slot>    
  </div>
</template>

<script>
    import BScroll from 'better-scroll'
    export default{
        data(){
            return{

            }
        },
        props:{
            probeType:{
                type:Number,
                default:1 // 1 表示节流 监听  3表示 不节流 的监听
            },
            click:{
                type:Boolean,
                default:true
            },
            lists:{
                type:Array,
                default:[]
            },
            listenScroll:{
                type:Boolean,
                default:false
            },
            // 是否开启上拉加载
            pullup:{
                type:Boolean,
                default:false
            },
            // better-scroll在滚动前会派发一个beforeScrollStart事件的
            beforeScroll:{
                type:Boolean,
                default:false
            },
            refreshDelay:{
                 type:Number,
                 default:20
            }
        },
        methods:{
            //  初始化 better-scroll 组件
            initScroll(){
                if(!this.$refs.wrapper){
                   return
                }else{
                    this.scroll=new BScroll(this.$refs.wrapper,{
                        probeType:this.probeType,
                        click:this.click
                    })
                }
                
                // 是否监听 scroll 组件的 滚动事件 ，返回的pos 是一个  x,y轴的滚动偏移位置
                if(this.listenScroll){
                    let _this= this;
                    this.scroll.on("scroll",(pos) =>{
                        _this.$emit('scrollpos',pos)
                    })
                }
                
                if(this.pullup){
                    this.scroll.on('scrollEnd',() =>{
                        if(this.scroll.y <= (this.scroll.maxScrollY + 50)){
                            this.$emit('scrollToEnd')
                        }
                    })
                }

                if(this.beforeScroll){
                    this.scroll.on('beforeScrollStart',() =>{
                        this.$emit('beforeScroll')
                    })
                }
            },
            enable(){
                this.scroll && this.scroll.enable()
            },
            disable(){
                this.scroll && this.scroll.disable()
            },
            refresh(){
                this.scroll && this.scroll.refresh()
            },
            scrollTo(){
              this.scroll && this.scroll.scrollTo.apply(this.scroll,arguments)
            }, 
            scrollToElement(){
              this.scroll && this.scroll.scrollToElement.apply(this.scroll,arguments)
            },
            test(){
                console.log("测试父子组件中的ref 与 children 属性的 通信")
            }
        },
        watch:{
          lists(){
             setTimeout(() =>{
                 this.refresh()
             },this.refreshDelay)
          }
        },
        mounted(){
            setTimeout(() =>{
                this.initScroll()
                console.log("qqqqqq")
                console.log(this.lists)
                console.log("bbbbbbbbbbbbb")
            },20)
        }
    }
</script>



